<script>
import { Doughnut } from 'vue-chartjs';
import Chart from "chart.js";

export default {
  extends: Doughnut ,
  name: 'doughnut',
  props: {
    Labels: Array,
    Data: Array,
  },
  data () {
    return {
      data: {
        labels: this.Labels,
        datasets: [
          {
            label: 'Bar Dataset',
            data: this.Data,
            backgroundColor: [
              '#f87979', '#aa4c8f', '#38b48b', '#006e54', '#c1e4e9', '#89c3eb', '#c3d825', 
            ],
            borderColor: 'transparent',
          },
        ]
      },
      options: {
        responsive: true,
        legend: false,
        // legend: {
        //   position: 'right' // ★この行追加
        // },
        // ★[tooltips]を追加
        tooltips: {
          bodyFontSize: 28,
          callbacks: {
            label: function(tooltipItem, data) {
              let total = 0 // 合計格納
              let indexItem = data.datasets[0].data[tooltipItem.index] // マウスを当てたデータ
              // 全データの合計算出
              data.datasets[0].data.forEach(item => {
                total += item
              })
              // パーセント表示
              return data.labels[tooltipItem.index] + ':' + indexItem + '(' + Math.round(indexItem / total * 100) + '%)'
            }
          }
        },
      }
    }
  },
  mounted () {
    this.renderChart(this.data, this.options)
    console.log(this.data);
    let total = 0;
    for (let _val of this.data.datasets[0].data) {
      total += parseInt(_val);
    }
    this.textCenter(total);
  },
  methods: {
    textCenter(val) {
      Chart.pluginService.register({
        beforeDraw: function(chart) {
          let width = chart.chart.width;
          let height = chart.chart.height;
          let ctx = chart.chart.ctx;

          ctx.restore();
          // let fontSize = (height / 114).toFixed(2);
          let fontSize = (height / 240).toFixed(2);
          ctx.font = fontSize + "em sans-serif";
          ctx.textBaseline = "middle";

          let text = val;
          // let textX = Math.round((width - ctx.measureText(text).width * 1.2) / 2);
          let textX = Math.round((width - ctx.measureText(text).width) / 2);
          let textY = height / 2;
          ctx.fillText(text, textX, textY);

          text = 'total:';
          textX = Math.round((width - ctx.measureText(text).width) / 2);
          textY = Math.round(height / 2 - fontSize * 12);
          ctx.font = (fontSize/2) + "em sans-serif";
          ctx.fillText('total:', textX, textY);

          ctx.save();
        }
      });

      Chart.plugins.unregister(this.chartdata);
    }
  }
 }
</script>